.Header {
  padding: 2rem 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1rem;
}

.Header__title {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

@media (max-width: 375px) {
  .Header {
    padding: 2rem;
  }
}

.Header__subtitle {
  text-align: center;
  font-size: 1rem;
  margin-bottom: 0.1rem;
}




