.WeatherAndForecast {
  display: flex;
  flex-direction: column;
  color: #f2f2f2;
  padding: 1rem 1rem; 
}

.WeatherAndForecast__container {
  display:flex;
  justify-content:space-between;
  padding-top: 1.5rem;
  padding: 1rem 2.5rem;
  width: 400px;
  margin-left: 0.5rem;
}

.inner_float_container {
  display: table-row;
}

.float_container {
  width: 100%;
  display: table;
}

.Select__button_day {
  margin-top: 0.5rem;
  margin-right: 0.5rem;
  margin-bottom: 15.0rem;
  padding: 0.5rem;
  font-weight: 500;
  background-color: #0dad42;
  border: 1px solid #a39908;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  align-content: center;
  width: 90%;
  cursor: pointer;
}

@media (max-width: 350px) {
  .WeatherAndForecast {
    padding: 2rem;
  }
}