@import url(https://fonts.googleapis.com/css2?family=Be+Vietnam+Pro:wght@400;500;700&display=swap);
.Search {
  margin-top: 1rem;
  width: 100%;
  display: flex;
  justify-content: center;
}

.Search__label,
.Search__button,
.Search__input {
  border: none;
  outline: none;
  font-size: 0.9rem;
  background-color: transparent;
  color: #fff;
  font-weight: 500;
}

.Search__label {
  border: 1px solid #f2f2f2;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  width: 75%;
  position: relative;
}

.Search__input {
  width: 100%;
  padding: 0.75rem 0rem 0.75rem 1.75rem;
  font-weight: 700;
}

.Search__input::-webkit-input-placeholder {
  color: #f2f2f2;
  opacity: 0.85;
}

.Search__input:-ms-input-placeholder {
  color: #f2f2f2;
  opacity: 0.85;
}

.Search__input::placeholder {
  color: #f2f2f2;
  opacity: 0.85;
}

.Search__button {
  padding: 0.75rem;
  font-weight: 700;
  background-color: #f37928;
  border: 1px solid #f37928;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  width: 25%;
  cursor: pointer;
}

.Header {
  padding: 2rem 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1rem;
}

.Header__title {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

@media (max-width: 375px) {
  .Header {
    padding: 2rem;
  }
}

.Header__subtitle {
  text-align: center;
  font-size: 1rem;
  margin-bottom: 0.1rem;
}





.Weather {
  display: flex;
  justify-content: space-between;
  padding: 1rem 0;
}

.Weather__info {
  text-align: left;
  display: flex;
}

.Weather__icon {
  width: 100px;
  height: 100px;
  -webkit-animation-name: sun;
          animation-name: sun;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes sun {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}

@keyframes sun {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}

.Weather__list {
  font-size: 0.85rem;
  list-style: none;
  margin-left: 0.75rem;
}

.list__temperature {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
  transition: scale 0.3s, padding-left 0.3s;
}

.list__temperature:hover {
  scale: 1.5;
  padding-left: 0.75rem;
}

.list__temperature-symbol {
  font-weight: 500;
  font-size: 1rem;
}

.Weather__other-info {
  text-align: right;
}

.other-info__city {
  font-size: 1.15rem;
  margin-bottom: 1rem;
}

.other-info__clouds {
  font-weight: normal;
  font-size: 0.9rem;
}

.Weather {
  padding: 0 0 1.5rem 0;
}

.Forecast__title {
  font-size: 0.9rem;
  text-align: center;
}

.Forecast__weather-icon {
  width: 60px;
  height: 60px;
}

.Forecast__weather-icon:hover {
  -webkit-animation-name: icons;
          animation-name: icons;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.Forecast__weather {
  margin-right: 1rem;
  border:0px solid;
  margin-top: "50px";
  margin: auto;
}

@-webkit-keyframes icons {
  0% {
    position: relative;
    top: 0;
  }
  50% {
    position: relative;
    top: 0.25rem;
  }
  100% {
    position: relative;
    top: 0;
  }
}

@keyframes icons {
  0% {
    position: relative;
    top: 0;
  }
  50% {
    position: relative;
    top: 0.25rem;
  }
  100% {
    position: relative;
    top: 0;
  }
}

.Forecast__temperature {
  display: flex;
  justify-content: center;
  
}

.temperature__max {
  margin-right: 0.2rem;
  color: #f37928;
}

.temperature__min {
  margin-left: 0.2rem;
}




.WeatherAndForecast {
  display: flex;
  flex-direction: column;
  color: #f2f2f2;
  padding: 1rem 1rem; 
}

.WeatherAndForecast__container {
  display:flex;
  justify-content:space-between;
  padding-top: 1.5rem;
  padding: 1rem 2.5rem;
  width: 400px;
  margin-left: 0.5rem;
}

.inner_float_container {
  display: table-row;
}

.float_container {
  width: 100%;
  display: table;
}

.Select__button_day {
  margin-top: 0.5rem;
  margin-right: 0.5rem;
  margin-bottom: 15.0rem;
  padding: 0.5rem;
  font-weight: 500;
  background-color: #0dad42;
  border: 1px solid #a39908;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  align-content: center;
  width: 90%;
  cursor: pointer;
}

@media (max-width: 350px) {
  .WeatherAndForecast {
    padding: 2rem;
  }
}
.Footer {
  margin: 3rem auto;
  width: -webkit-max-content;
  width: max-content;
  font-size: 0.9rem;
  font-weight: bold;
  text-align: center;
}

.Footer p {
  display: flex;
  align-items: center;
}

.Footer img {
  height: 22px;
  width: 22px;
}

.Footer a {
  color: inherit;
}

.Loader {
  margin: 5rem auto;
  border: 8px solid #f2f2f2;
  border-top: 8px solid #f37928;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  -webkit-animation: spin 1.5s linear infinite;
          animation: spin 1.5s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Warning {
  display: flex;
  flex-direction: column;
  padding: 2rem 3rem;
  text-align: center;
}

.Warning > * {
  padding: 0.15rem 0;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  font-size: 16px;
  font-family: Be Vietnam Pro, sans-serif;
  color: #f2f2f2;
  background-image: url(/previsao_tempo_react_js/static/media/background.551232ea.jpg);
}

.App {
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.45);
}

.App__container {
  max-width: 500px;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

